"use client";

import { useState, useEffect } from "react";
import { ArrowRight, MapPin, Clock, Navigation as NavigationIcon } from "lucide-react";
import EnhancedAirportMap from "@/components/map/EnhancedAirportMap";
import { Location } from "@/lib/types";
import { findRoute } from "@/lib/utils/navigation";
import airportData from "@/lib/data/airport-data.json";
import { theme } from "@/lib/config/theme";

export default function NavigationPage() {
  const [locations, setLocations] = useState<Location[]>([]);
  const [floors, setFloors] = useState<{ level: number; name: string }[]>([]);
  const [currentFloor, setCurrentFloor] = useState(0);
  const [startLocation, setStartLocation] = useState<string>("");
  const [endLocation, setEndLocation] = useState<string>("");
  const [route, setRoute] = useState<[number, number][] | undefined>();
  const [routeSteps, setRouteSteps] = useState<any[]>([]);
  const [totalDistance, setTotalDistance] = useState(0);
  const [totalDuration, setTotalDuration] = useState(0);

  useEffect(() => {
    setLocations(airportData.locations as Location[]);
    setFloors(airportData.floors);
  }, []);

  const handleCalculateRoute = () => {
    if (!startLocation || !endLocation) {
      return;
    }

    const start = locations.find((loc) => loc.id === startLocation);
    const end = locations.find((loc) => loc.id === endLocation);

    if (!start || !end) return;

    const calculatedRoute = findRoute(start, end, locations);

    if (calculatedRoute) {
      setRoute(calculatedRoute.path);
      setRouteSteps(calculatedRoute.instructions);
      setTotalDistance(calculatedRoute.distance);
      setTotalDuration(calculatedRoute.duration);
      setCurrentFloor(start.floor);
    }
  };

  const groupedLocations = locations.reduce((acc, loc) => {
    const type = loc.type;
    if (!acc[type]) {
      acc[type] = [];
    }
    acc[type].push(loc);
    return acc;
  }, {} as Record<string, Location[]>);

  const locationTypeLabels: Record<string, string> = {
    entrance: "Entrées",
    gate: "Portes d'embarquement",
    checkin: "Enregistrement",
    security: "Sécurité",
    toilet: "Toilettes",
    restaurant: "Restauration",
    shop: "Boutiques",
    information: "Information",
    lounge: "Salons",
    baggage: "Bagages",
    medical: "Services médicaux",
  };

  return (
    <div className="h-[calc(100vh-64px)] flex flex-col md:flex-row"
      style={{ backgroundColor: theme.colors.background.paper }}
    >
      {/* Sidebar */}
      <div
        className="w-full md:w-96 flex flex-col border-r"
        style={{
          backgroundColor: theme.colors.background.elevated,
          borderColor: theme.colors.border.main,
        }}
      >
        <div
          className="border-b"
          style={{
            padding: theme.spacing[6],
            borderColor: theme.colors.border.main,
          }}
        >
          <h2
            className="font-semibold mb-2"
            style={{
              fontSize: theme.typography.h3.fontSize,
              fontWeight: theme.typography.h3.fontWeight,
              color: theme.colors.text.primary,
            }}
          >
            Navigation
          </h2>
          <p
            style={{
              fontSize: theme.typography.small.fontSize,
              color: theme.colors.text.secondary,
            }}
          >
            Calculez votre itinéraire dans l'aéroport
          </p>
        </div>

        <div className="flex-1 overflow-y-auto" style={{ padding: theme.spacing[6] }}>
          <div className="space-y-4">
            {/* Start Location */}
            <div>
              <label
                className="block font-medium mb-2"
                style={{
                  fontSize: theme.typography.small.fontSize,
                  color: theme.colors.text.primary,
                }}
              >
                Point de départ
              </label>
              <select
                value={startLocation}
                onChange={(e) => setStartLocation(e.target.value)}
                className="w-full rounded-lg focus:outline-none transition-all"
                style={{
                  padding: `${theme.spacing[3]} ${theme.spacing[4]}`,
                  border: `1px solid ${theme.colors.border.main}`,
                  fontSize: theme.typography.base.fontSize,
                  color: theme.colors.text.primary,
                  backgroundColor: theme.colors.background.default,
                }}
                onFocus={(e) => {
                  e.currentTarget.style.borderColor = theme.colors.accent.main;
                  e.currentTarget.style.boxShadow = `0 0 0 3px ${theme.colors.accent.main}20`;
                }}
                onBlur={(e) => {
                  e.currentTarget.style.borderColor = theme.colors.border.main;
                  e.currentTarget.style.boxShadow = 'none';
                }}
              >
                <option value="">Sélectionner...</option>
                {Object.entries(groupedLocations).map(([type, locs]) => (
                  <optgroup key={type} label={locationTypeLabels[type] || type}>
                    {locs.map((loc) => (
                      <option key={loc.id} value={loc.id}>
                        {loc.name}
                      </option>
                    ))}
                  </optgroup>
                ))}
              </select>
            </div>

            {/* End Location */}
            <div>
              <label
                className="block font-medium mb-2"
                style={{
                  fontSize: theme.typography.small.fontSize,
                  color: theme.colors.text.primary,
                }}
              >
                Destination
              </label>
              <select
                value={endLocation}
                onChange={(e) => setEndLocation(e.target.value)}
                className="w-full rounded-lg focus:outline-none transition-all"
                style={{
                  padding: `${theme.spacing[3]} ${theme.spacing[4]}`,
                  border: `1px solid ${theme.colors.border.main}`,
                  fontSize: theme.typography.base.fontSize,
                  color: theme.colors.text.primary,
                  backgroundColor: theme.colors.background.default,
                }}
                onFocus={(e) => {
                  e.currentTarget.style.borderColor = theme.colors.accent.main;
                  e.currentTarget.style.boxShadow = `0 0 0 3px ${theme.colors.accent.main}20`;
                }}
                onBlur={(e) => {
                  e.currentTarget.style.borderColor = theme.colors.border.main;
                  e.currentTarget.style.boxShadow = 'none';
                }}
              >
                <option value="">Sélectionner...</option>
                {Object.entries(groupedLocations).map(([type, locs]) => (
                  <optgroup key={type} label={locationTypeLabels[type] || type}>
                    {locs.map((loc) => (
                      <option key={loc.id} value={loc.id}>
                        {loc.name}
                      </option>
                    ))}
                  </optgroup>
                ))}
              </select>
            </div>

            <button
              onClick={handleCalculateRoute}
              disabled={!startLocation || !endLocation}
              className="w-full rounded-lg font-medium transition-all"
              style={{
                padding: `${theme.spacing[3]} ${theme.spacing[6]}`,
                backgroundColor: (!startLocation || !endLocation)
                  ? theme.colors.gray[300]
                  : theme.colors.accent.main,
                color: (!startLocation || !endLocation)
                  ? theme.colors.gray[500]
                  : theme.colors.accent.contrast,
                fontSize: theme.typography.button.fontSize,
                fontWeight: theme.typography.button.fontWeight,
                boxShadow: (!startLocation || !endLocation) ? 'none' : theme.shadow.sm,
                cursor: (!startLocation || !endLocation) ? 'not-allowed' : 'pointer',
              }}
              onMouseEnter={(e) => {
                if (startLocation && endLocation) {
                  e.currentTarget.style.backgroundColor = theme.colors.accent.dark;
                  e.currentTarget.style.transform = 'translateY(-1px)';
                  e.currentTarget.style.boxShadow = theme.shadow.md;
                }
              }}
              onMouseLeave={(e) => {
                if (startLocation && endLocation) {
                  e.currentTarget.style.backgroundColor = theme.colors.accent.main;
                  e.currentTarget.style.transform = 'translateY(0)';
                  e.currentTarget.style.boxShadow = theme.shadow.sm;
                }
              }}
            >
              Calculer l'itinéraire
            </button>

            {/* Route Info */}
            {route && (
              <div
                className="mt-6 rounded-lg"
                style={{
                  padding: theme.spacing[4],
                  backgroundColor: theme.colors.accent.main + '10',
                  border: `1px solid ${theme.colors.accent.main}30`,
                }}
              >
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center space-x-2">
                    <MapPin
                      className="w-5 h-5"
                      style={{ color: theme.colors.accent.dark }}
                    />
                    <span
                      className="font-semibold"
                      style={{
                        color: theme.colors.accent.dark,
                        fontSize: theme.typography.base.fontSize,
                      }}
                    >
                      {totalDistance.toFixed(0)}m
                    </span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Clock
                      className="w-5 h-5"
                      style={{ color: theme.colors.accent.dark }}
                    />
                    <span
                      className="font-semibold"
                      style={{
                        color: theme.colors.accent.dark,
                        fontSize: theme.typography.base.fontSize,
                      }}
                    >
                      ~{totalDuration} min
                    </span>
                  </div>
                </div>

                <div className="space-y-3">
                  {routeSteps.map((step, index) => (
                    <div key={index} className="flex items-start space-x-3">
                      <div
                        className="w-7 h-7 rounded-full flex items-center justify-center flex-shrink-0 font-semibold"
                        style={{
                          backgroundColor: theme.colors.accent.main,
                          color: theme.colors.accent.contrast,
                          fontSize: theme.typography.tiny.fontSize,
                        }}
                      >
                        {index + 1}
                      </div>
                      <div className="flex-1">
                        <p
                          className="font-medium"
                          style={{
                            color: theme.colors.text.primary,
                            fontSize: theme.typography.small.fontSize,
                          }}
                        >
                          {step.instruction}
                        </p>
                        {step.distance > 0 && (
                          <p
                            className="mt-1"
                            style={{
                              fontSize: theme.typography.tiny.fontSize,
                              color: theme.colors.text.secondary,
                            }}
                          >
                            {step.distance.toFixed(0)}m
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Map */}
      <div className="flex-1 relative">
        <EnhancedAirportMap
          locations={locations}
          selectedLocation={endLocation}
          showRoute={route}
          currentFloor={currentFloor}
        />

        {/* Floor Selector */}
        <div
          className="absolute top-4 left-4 rounded-xl overflow-hidden"
          style={{
            backgroundColor: theme.colors.background.elevated,
            boxShadow: theme.shadow.lg,
          }}
        >
          {floors.map((floor) => {
            const isActive = currentFloor === floor.level;
            return (
              <button
                key={floor.level}
                onClick={() => setCurrentFloor(floor.level)}
                className="block w-full text-left transition-all"
                style={{
                  padding: `${theme.spacing[4]} ${theme.spacing[6]}`,
                  borderBottom: `1px solid ${theme.colors.border.light}`,
                  backgroundColor: isActive
                    ? theme.colors.accent.main
                    : theme.colors.background.elevated,
                  color: isActive ? theme.colors.accent.contrast : theme.colors.text.primary,
                }}
                onMouseEnter={(e) => {
                  if (!isActive) {
                    e.currentTarget.style.backgroundColor = theme.colors.gray[50];
                  }
                }}
                onMouseLeave={(e) => {
                  if (!isActive) {
                    e.currentTarget.style.backgroundColor = theme.colors.background.elevated;
                  }
                }}
              >
                <div
                  className="font-semibold"
                  style={{ fontSize: theme.typography.base.fontSize }}
                >
                  {floor.level === 0 ? "RDC" : `Étage ${floor.level}`}
                </div>
                <div
                  className="mt-1"
                  style={{
                    fontSize: theme.typography.tiny.fontSize,
                    color: isActive
                      ? theme.colors.accent.contrast + 'CC'
                      : theme.colors.text.secondary,
                  }}
                >
                  {floor.name.split(" - ")[1] || floor.name}
                </div>
              </button>
            );
          })}
        </div>
      </div>
    </div>
  );
}
